.widget-area .widget {
  padding: 1em;
  box-shadow: 0 -5px 0 0 rgba(240, 240, 240, 0.75), 0 5px 0 0 rgba(240, 240, 240, 0.75);
  border-radius: var(--main-border-radius);

  a,
  a:visited,
  a:hover {
    color: #037b8c;
  }

  a:hover {
    text-decoration: underline;
  }
}

.widget-area .widget_search {
  padding-top: 0.25em;
  padding-bottom: 0.25em;

  .search-form {
    margin-right: 0.25em;
  }

  .search-field {
    min-height: 28px;
    padding: 0 1.5em 0 0;
    background: url('../img/search.png') right center no-repeat;
    border: 0;
    text-align: right;
    font-size: 0.9em;
    box-sizing: border-box;

    @include placeholder {
      color: #ccc;
    }
  }
}